	module.exports = {"data":{"markdownRemark":{"html":"<p>朝の早朝（父親の名前で）、アイルランド共和国軍と英国当局との関係に基づいた政治的映画であり、そこに深刻な矛盾があり、そのような矛盾が政治や人々の感情に及ぼす影響が反映されています。社会にいた4人の若者たちは、警察が爆発の主人であると非難され、警察はそれらを募集するためにさまざまな方法を使い、最終的に目標を達成しました。そして彼らはまた、いくつかの親族を刑務所に入れました。実際の共和党軍が捕らえられ、説明された後、警察は依然として事件を取り消さず、刑務所にとどまった。その後、父親は刑務所で死亡した。女性弁護士は、辛抱強く努力した結果、真実を知り、15年間投獄された後、裁判所からすべて解放された。そのような経験の後、若い男は成熟したように見え、最後に彼は真実を確かめなければならないと言った。しかし、最後に明確な結果がないようです。</p>\n<p>映画は本当の社会的背景を持ち、西洋社会に存在する非合理的な政治的・非民主的政治を反映している。この種の政治の出現は、心理的な拡大と社会的矛盾の結果である。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0161.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0161.md","frontmatter":{"title":"10月18日 火曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0161.md absPath of file >>> MarkdownRemark"}}

